html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}
article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}
html{height:100%}
body{line-height:1}
ol,ul{list-style:none}
blockquote,q{quotes:none}
blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}
table{border-collapse:collapse;border-spacing:0}

.App {
  font-family: Onest, system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  max-width: 1440px;
	width: 100%;
	margin: auto;
	font-size: 16px;
	padding: 0 30px;
}

img {
	vertical-align: middle;
	max-width: 100%;
	max-height: 100%;
	object-fit: cover;
}


@font-face {
  font-family: 'Onest';
  src: url('fonts/Onest-Regular.eot');
  src: url('fonts/Onest-Regular.eot?#iefix') format('embedded-opentype'),
      url('fonts/Onest-Regular.woff2') format('woff2'),
      url('fonts/Onest-Regular.woff') format('woff'),
      url('fonts/Onest-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Onest';
  src: url('fonts/Onest-Bold.eot');
  src: url('fonts/Onest-Bold.eot?#iefix') format('embedded-opentype'),
      url('fonts/Onest-Bold.woff2') format('woff2'),
      url('fonts/Onest-Bold.woff') format('woff'),
      url('fonts/Onest-Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Onest';
  src: url('fonts/Onest-Medium.eot');
  src: url('fonts/Onest-Medium.eot?#iefix') format('embedded-opentype'),
      url('fonts/Onest-Medium.woff2') format('woff2'),
      url('fonts/Onest-Medium.woff') format('woff'),
      url('fonts/Onest-Medium.ttf') format('truetype');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

:root {
	--grcol:#5BD8BF;
	--bckcol:#F4FBEF;
	--whitecol:#F5F5F5;
	--graycol:#949494;
	--darkgraycol:#5F5F5F;
	--lightblck:#2A2A2A;
}

.container {
	max-width: 1440px;
	width: 100%;
	margin: auto;
	font-size: 16px;
	padding: 0 30px;
}

.navbar {
	background-color: var(--bckcol);
	padding: 16px 0;
}

.navbar--inner {
	display: flex;
	justify-content: space-between;
	color: var(--darkgraycol);
	align-self: stretch;
}

.navbar-textbox {
	display: flex;
	align-items: center;
	gap: 16px;
}

.navbar2 {
	display: flex;
	justify-content: space-between;
	margin-top: 20px;
	align-items: center;
	gap: 20px;
}

.logo {
	width: 164px;
	height: 37px;
}

.croplogo {
	display: none;
	width: 40px;
	height: 40px;
}

.search {
	display: flex;
    justify-content: center;
	align-items: center;
	width: 412px;
	padding: 12px 16px;
	border-radius: 12px;
	background-color: var(--whitecol);
	color: var(--graycol);
	gap: 8px;
}

.search-pic {
	width: 24px;
	height: 24px;
	flex-shrink: 0;
}

.location {
	display: flex;
	flex-direction: column;
	position: relative;
	flex-shrink: 0;
}

.Makh {
	color: var(--darkgraycol);
	font-size: 14px;
	font-style: normal;
	font-weight: 400;
	line-height: 20px;
}

.arrow {
	width: 16px;
	height: 16px;
}

ul {
	list-style: none;
	width: 320px;
}

ul li {
	display: block;
	position: relative;
	text-align: left;
}

.dropdown {
	display: flex;
	flex-direction: column;
	background-color: var(--whitecol);
	list-style-type: none;
	border-radius: 16px;
	box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.08);
	gap: 16px;
	padding: 20px 16px;
}

.dropping {
	padding-top: 12px;
	display: none;
	position: absolute;
	top: 100%;
	right: 0;
}

.dropping:hover {
	display: flex;
}

 .element {
	cursor: default;
	text-decoration: none;
	 color: var(--darkgraycol);
	 font-size: 14px;
	 font-style: normal;
	 font-weight: 700;
	 line-height: 18px;
}

 .element:hover ~ .dropping {
	 display: block;
 }

 .element--inner {
	 font-size: 16px;
	 font-style: normal;
	 font-weight: 400;
	 line-height: 22px;
	 letter-spacing: 0.16px;
	 display: flex;
	 gap: 12px;
	 align-items: center;
 }

 .element--inner img {
	 width: 24px;
	 height: 24px;
 }

 .element-info {
	 flex-grow: 1;
 }

.element-inner--in {
	display: flex;
	font-size: 12px;
	font-style: normal;
	font-weight: 400;
	line-height: 16px;
	letter-spacing: 0.12px;
	align-items: flex-start;
	color: var(--graycol);
}

.search--text {
	font-size: 14px;
	font-style: normal;
	font-weight: 400;
	line-height: 20px;
}

.street {
	font-size: 14px;
	font-style: normal;
	font-weight: 700;
	line-height: 18px;
}

.button2 {
	display: flex;
	justify-content: space-between;
	gap: 8px;
}
.textboxcrate {
	color: white;
	border-radius: 32px;
	background-color: var(--grcol);
	width: 144px;
	height: 48px;
	display: flex;
    justify-content: center;
	align-items: center;
	gap: 12px;
}

.banners {
	margin-top: 20px;
	width: 100%;
	overflow: auto;
	display: flex;
	justify-content: space-between;
	gap: 14px;
	align-items: center;
}

.banner--1 {
	min-width: 300px;
}

.banner--2 {
	min-width: 300px;
}

.banner--3 {
	min-width: 300px;
}

.categories {
	display: flex;
	align-items: center;
	gap: 28px;
	font-weight: bold;
	margin-top: 20px;
	justify-content: center;
	width: 100%;
	overflow: auto;
	height: 130px;
}

.category {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 90px;
}

.category--pic {
	min-width: 100px;
	height: 90px;
}

.header2 {
	font-size: 28px;
	font-style: normal;
	font-weight: 500;
	line-height: 36px;
}

.pic1 {
	border-radius: 10%;
}

.navbar {
	display: flex;
	justify-content: space-between;
}

.popular {
	font-size: 28px;
	font-weight: bold;
}

.navbar3 {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-top: 40px;
}

.popular-inner {
	display: flex;
	justify-content: start;
	align-items: center;
}

.carousel {
	display: flex;
	gap: 32px;
	margin-top: 32px;
	overflow-x: auto;
	width: 100%;
}

.timebox {
	display: flex;
	align-items: center;
}

.oclock {
	width: 20px;
height: 20px;
}

.headcar {
	font-weight: bold;
}

.favour {
	position: absolute;
	right: 12px;
	top: 11px;
}

.card {
	position: relative;
	min-width: 280px;
}

.textbox {
	display: flex;
	flex-direction: column;
	letter-spacing: 0.28px;
	line-height: 22px;
}	

.rests2 {
	margin-top: 20px;
	font-size: 28px;
	font-weight: bold;
	margin-bottom: 32px;
}

.dishes {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	gap: 32px;
	width: 100%;
}

.card2 {
	position: relative;
	max-width: 458px;
}

.text2 {
	display: flex;
	align-items: center;
	gap: 8px;
}

.newtime {
	display: flex;
	align-items: center;
}

.reviews {
	display: flex;
	gap: 8px;
}

.textbox2 {
	margin-top: 16px;
}

.pic2 {
	display: flex;

	width: 460px;
	height: 230px;
	border-radius: 5%;
}

.favour2 {
	position: absolute;
	right: 22px;
	top: 10px;
}

.BK {
	font-weight: bold;
}

.friend {
	font-weight: bold;
}

.TW {
	font-weight: bold;
}

.presentation {
	display: flex;
	justify-content: center;
}

.big-banner {
	display: flex;
	justify-content: center;
	align-items: center;
}

.cut-banner {
	display: none;
	margin-top: 16px;
	justify-content: center;
}

.footer {
	display: flex;
	width: 100%;
	max-width: 1440px;
	padding: 72px 0;
	height: 100%;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
}

.footer--text {
	display: flex;
	flex-direction: column;
	justify-content: center;
	font-size: 16px;
	font-weight: 700;
	line-height: 22px;
	font-style: normal;
	color: var(--graycol);
}

.footer--text--inner {
	font-size: 16px;
	font-style: normal;
	font-weight: 700;
	line-height: 22px;
	color: var(--lightblck);
}

.footer1 {
	display: flex;
	justify-content: space-between;
	width: 100%;
}

.footer--logo {
	width: 164px;
	height: 37px;
}

@media (max-width: 1100px){
	.navbar {
		display: none;
	}

	.navbar2 {
		display: flex;
		flex-wrap: wrap;
		overflow-wrap: break-word;
	}

	.search {
		display: flex;
		order: 3;
		align-items: center;
		justify-content: center;
		color: var(--graycol);
		border-radius: 12px;
		width: 412px;
		height: 48px;
	}

	.location {
		font-size: 14px;
	}

	.button2--text {
		display: none;
	}

	.smile {
		width: 40px;
		height: 40px;
	}

	.textboxcrate {
		height: 40px;
		width: 40px;
		border-radius: 12px;
	}

	.croplogo {
		display: flex;
	}

	.logo {
		display: none;
	}

	.dishes {
		display: flex;
		flex-wrap: wrap;
		overflow-wrap: break-word;
		justify-content: center;
	}

	.big-banner {
		display: none;
	}

.cut-banner {
	display: flex;
}

	.footer1 {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		gap: 28px;
		text-align: left;
		align-items: flex-start;

	}

	.footer {
		display: flex;
		justify-content: flex-start;
	}
}